import React, {useState} from 'react'
import {FileText, Map, BarChart} from 'react-feather'

import colors from '../styles/colors'

import DateNav from '../components/date-nav'
import Menu from '../components/menu'
import ReactMapGl from '../components/react-map-gl'
import Statistics from '../components/statistics'
import ConfirmedChart from '../components/confirmed-chart'
import Description from '../components/descritpion'
import Footer from '../components/footer'

const VIEWS = {
  map: ({viewport, date, regionsReport, setViewport}) => (
    <ReactMapGl
      viewport={viewport}
      date={date}
      regions={regionsReport}
      onViewportChange={setViewport} />
  ),
  stats: ({date, franceReport}) => (
    <>
      <Statistics report={franceReport} />

      {franceReport && franceReport.history && (
        <ConfirmedChart data={franceReport.history.filter(r => date >= r.date)} height={300} />
      )}
    </>
  ),
  description: () => <Description />
}

const MobilePage = props => {
  const [selectedView, setSelectedView] = useState('map')

  return (
    <div className='mobile-page-container'>
      <DateNav {...props} />
      <Menu selectedView={selectedView} selectView={setSelectedView}>
        {VIEWS[selectedView](props)}
      </Menu>
      <div className='view-selector'>
        <div className={`${selectedView === 'description' ? 'selected' : ''}`} onClick={() => selectView('description')}>
          <FileText />
        </div>
        <div className={`${selectedView === 'map' ? 'selected' : ''}`} onClick={() => selectView('map')}>
          <Map />
        </div>
        <div className={`${selectedView === 'stats' ? 'selected' : ''}`} onClick={() => selectView('stats')}>
          <BarChart />
        </div>
      </div>
      <Footer />

      <style jsx>{`
        .mobile-page-container {
          height: 100%;
        }

        .view-selector {
          display: none;
        }

        .view-selector > div {
          padding: 1em;
          margin: auto;
        }

        .view-selector > div.selected {
          border-top: 2px solid ${colors.blue};
        }
      `}</style>
    </div>
  )
}

export default MobilePage
